.CustomCheckbox {
	display: block;
	position: relative;
	padding-left: 28px;
	margin-bottom: 12px;
	cursor: pointer;
	color: #444343;
	font-size: 14px;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
}

.CustomCheckbox input {
	position: absolute;
	opacity: 0;
	cursor: pointer;
	height: 0;
	width: 0;
}

.CustomCheckbox .checkmark {
	position: absolute;
	top: 0;
	left: 0;
	height: 24px;
	width: 24px;
	background-image: url(data:image/png;base64,);
	background-size: cover;
}

.CustomCheckbox input:checked ~ .checkmark {
	background-image: url(data:image/png;base64,);
}

