#Dashboard {
	padding: 24px 75px;
}

#Dashboard .box-area {
	background-color: white;
	padding: 24px;
	border-radius: 8px;
}

#Dashboard #profile #username {
	font-weight: 500;
	font-size: 20px;
}

#Dashboard #profile #id {
	margin-top: 8px;
	font-size: 20px;
}

#Dashboard #profile #role {
	margin-top: 14px;
	font-size: 16px;
}

#Dashboard #profile #email {
	margin-top: 14px;
	font-size: 16px;
}

#Dashboard #login-info-container {
	position: relative;
	height: 200px;
	text-align: center;
}

#Dashboard .info-title {
	font-size: 12px;
	color: #6d7278;
}

#Dashboard #login-info-1 .info-title, #Dashboard #login-info-2 .info-title {
	margin: 8px 0;
}

#Dashboard #login-info-3 .info-title, #Dashboard #login-info-4 .info-title {
	margin-top: 15px;
}

#Dashboard .info-content {
	font-weight: 500;
}

#Dashboard #login-info-1 .info-content, #Dashboard #login-info-2 .info-content {
	font-size: 14px;
}

#Dashboard #login-info-3 .info-content, #Dashboard #login-info-4 .info-content {
	/* margin-top: 16px;
	font-size: 24px; */
	font-size: 14px;
}

#Dashboard #login-info-1 {
	border-right: 1px solid #97979788;
	border-bottom: 1px solid #97979788;
	width: 50%;
	height: 105px;
	position: absolute;
	top: 0;
	left: 0;
}

#Dashboard #login-info-2 {
	border-left: 1px solid #97979788;
	border-bottom: 1px solid #97979788;
	width: 50%;
	height: 105px;
	position: absolute;
	top: 0;
	left: 50%;
}

#Dashboard #login-info-3 {
	border-right: 1px solid #97979788;
	border-top: 1px solid #97979788;
	width: 50%;
	height: 105px;
	position: absolute;
	top: 105px;
	left: 0;
}

#Dashboard #login-info-4 {
	border-left: 1px solid #97979788;
	border-top: 1px solid #97979788;
	width: 50%;
	height: 105px;
	position: absolute;
	top: 105px;
	left: 50%;
}

#Dashboard .activity #time {
	font-size: 16px;
}

#Dashboard .activity #action {
	font-weight: 500;
	font-size: 16px;
}

#Dashboard .activity #username {
	font-size: 16px;
}

#Dashboard .summary {
	margin-top: 6px;
	font-size: 30px;
}

