.Dropzone {
	border: 1px dashed #8c181b;
	height: 200px;
	position: relative;
}

.Dropzone #content{
	margin: 0;
	position: absolute;
	top: 50%;
	left: 50%;
	-ms-transform: translate(-50%, -50%);
	transform: translate(-50%, -50%);
	text-align: center;
	color: #72777c;
	font-size: 13px;
}

.Dropzone #select-button{
	background-color: #f7f7f7;
	border: 1px solid #cccccc;
	border-radius: 3px;
	color: #555555;
	box-shadow: 0 1px #cccccc80;
}

.Dropzone #img-size{
	width: 600px;
      height: 200px;
}
.Dropzone #pdf-size{
	width: 50px;
      height: 50px;
}