.button-white,
.button-red,
.button-green {
  width: 296px;
  height: 40px;
  text-align: center;
  padding-top: 8;
  border-radius: 32px;
  border: 1px solid #8c181b;
}
.button-green {
  background-color: #34851c;
  color: white;
}

.button-white {
  background-color: white;
  color: #8c181b;
}

.button-white:disabled,
.button-white[disabled] {
  opacity: 0.5;
}

.button-white:hover {
  background-color: rgb(209, 197, 197);
}

.button-red {
  background-color: #8c181b;
  color: white;
}

.button-red:hover {
  background-color: #680f12;
}

.button-white-small,
.button-red-small {
  width: 180px;
  height: 40px;
  text-align: center;
  padding-top: 8;
  border-radius: 32px;
  border: 1px solid #8c181b;
  margin-right: 10px;
}

.button-white-small {
  background-color: white;
  color: #8c181b;
}

.button-red-small {
  background-color: #8c181b;
  color: white;
}

.button-white-popup {
  background-color: white;
  color: #8c181b;
}
