#Login {
	position: relative;
	background-color:#e4e1e1;
	padding: 50px 0;
}

#Login #form {
	padding: 20px 120px;
	margin: 0 auto;
	width: 470px;
	height: 440px;
	border-radius: 14px;
	text-align: center;
	background-color: white;
}

#Login #title {
	color: black;
	font-size: 36px;
	margin-bottom: 56px;
}

#Login input {
	display: block;
	margin: 10px auto;
	padding: 8px 0;
	border: 0;
	width: 100%;
	border-bottom: 1px solid #8b8b8b; 
	font-size: 14px;
	/* border-bottom-color : black; */
}

#Login button {
	width: 80%;
	height: 28px;
	margin-top: 70px;
	margin-bottom: 16px;
	font-size: 12px;
}

#Login #forgot-pass {
	color: #8c181b;
	font-size: 12px;
}

#Login #note {
	margin-top: 20px;
	font-size: 12px;
}