.loading-overlay{
    position: fixed;
    background: rgba(0,0,0,0.3);
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 100;
}
.loading-overlay .loader{
    width: 100px;
    height: 100px;
    position: fixed;
    background: url('./../../../assets/images/loader.svg'), transparent;
    z-index: 101;
    top: calc(50% - 50px);
    left: calc(50% - 50px);
    background-position: center;
    background-repeat: no-repeat;
}