#ListParamProductHolding {
	padding: 29px 50px;
}
.product-list .single-product{
	background: #fff;
	padding: 20px;
	margin: 15px 0;
	cursor: pointer;
	position: relative;
	-webkit-box-shadow: 5px 5px 7px rgba(0,0,0,0.3);
	        box-shadow: 5px 5px 7px rgba(0,0,0,0.3);
	-webkit-transition: 0.3s;
	-o-transition: 0.3s;
	transition: 0.3s;
	padding-right: 60px;
	min-height: 64px;
}

.product-list .single-product:hover{
	-webkit-box-shadow: 5px 5px 7px rgba(0,0,0,0.7);
	        box-shadow: 5px 5px 7px rgba(0,0,0,0.7);
}
.SortableHelperWithOverride {
	pointer-events: auto !important;
	z-index: 10;
	background: #fff;
	padding: 20px;
	margin: 15px 0;
	cursor: pointer;
	position: relative;
	-webkit-box-shadow: 5px 5px 7px rgba(0,0,0,0.3);
	        box-shadow: 5px 5px 7px rgba(0,0,0,0.3);
}
.btn-edit{
	cursor: pointer;
	position: absolute;
	right: 0;
	top:calc(50% - 33px);
	padding: 18px;
}
.btn-edit img{
	width: 30px;
	border: none !important;
}
.modalEditProduct{
	position: absolute;
    top: calc(50% - 125px);
    left:  calc(50% - 30%);
    border: 1px solid rgb(204, 204, 204);
    background: rgb(255, 255, 255);
    overflow: auto;
    border-radius: 4px;
    outline: none;
	padding: 20px;
	width: 60%;
	height: 250px;
}
.add-new-product{
	background: #fff;
	padding: 20px;
	margin: 16px 0;
	position: relative;
	cursor: pointer;
	text-align: center;
	font-weight: bold;
	-webkit-box-shadow: 5px 5px 7px rgba(0,0,0,0.3);
	        box-shadow: 5px 5px 7px rgba(0,0,0,0.3);
	-webkit-transition: 0.3s;
	-o-transition: 0.3s;
	transition: 0.3s;
}
.add-new-product:hover{
	-webkit-box-shadow: 5px 5px 7px rgba(0,0,0,0.7);
	        box-shadow: 5px 5px 7px rgba(0,0,0,0.7);
}
.add-icon{
	font-size: 22px;
    line-height: 1;
    margin-right: 16px;
}
.single-wrapper{
	position: relative;
}
.status-title{
	font-size: 10px;
}
.remark-title{
	font-size: 12px;
}