.AddButton {
    height: 70px;
    display: flex;
    align-items: center;
    margin-top: 5px;
}

.AddButton-Inner {
    width: 100%;
    padding: 8px;
    margin: 16px 0px;
    position: relative;
    cursor: pointer;
    text-align: left;
    font-weight: bold;
    -webkit-box-shadow: 5px 5px 7px rgba(0,0,0,0.3);
            box-shadow: 5px 5px 7px rgba(0,0,0,0.3);
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
    text-align: center;
}

.AddButton-Inner:hover:active {
    background-color: rgb(250, 250, 250);    
    border: 1px solid rgb(235, 235, 235);
}