  .Modal.client-form-modal {
  position: fixed;
  top: calc(50% - 100px);
  left: calc(50% - 329px);
  right: auto;
  bottom: auto;
  width: 658px;
  min-height: 200px;
  background-color: #fff;
  border-radius: 20px;
  padding: 32px;
  }
  .Modal.client-form-modal.search{
    left: calc(50% - 500px);
    width: 1000px;
    height: 660px;
    top: calc(50% - 330px);
    overflow: hidden;
  }
  .client-form-modal .rdt_TableBody{
    overflow: auto;
    height: 295px;
    position: relative;
  }

  .Overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0,0,0,0.5);
  }
  .result-search{
  margin: 5px;
  margin-top: 32px;
  border: 1px solid #000;
  }
  #ClientManagementForm .kJdJTV{
  padding-top: 50px;
  }
  .result-search .bIxPIp{
  min-height: 200px;
  max-height: 300px;
  overflow-x: auto;
  }
  #ClientManagementForm .updated label, .updated div{
  color: #e28f0a !important;
  }
  #ClientManagementForm .validator-error-message div{
  color: red !important;
  }
  .with-r-label input{
  width: calc(100% - 30px);
  border-top-right-radius: 0px;
  border-bottom-right-radius: 0px;
  border-right: 0px;
  }
  .with-r-label .r-label{
  border: 1px solid #979797;
  text-align: center;
  width: 30px;
  background: #ded8d8;
  color: #000;
  border-radius: 4px;
  border-top-left-radius: 0px;
  border-bottom-left-radius: 0px;
  padding: 3px 0;
  }