.popup {  
    z-index: 1;
    position: fixed;  
    width: 100%;  
    height: 100%;  
    top: 0;  
    left: 0;  
    right: 0;  
    bottom: 0;  
    margin: auto;  
    background-color: rgba(0,0,0, 0.5);  
  }  
  .popup_inner {  
    padding: 2%;
    position: absolute;  
    left: 25%;  
    right: 25%;  
    top: 5%;  
    bottom: 25%;  
    margin: auto;  
    border-radius: 20px;  
    background-color: #dbdcdc;  
  }