.Modal {
    position: fixed;
    top: calc(50% - 100px);
    left: calc(50% - 329px);
    right: auto;
    bottom: auto;
    width: 658px;
    min-height: 200px;
    background-color: #fff;
    border-radius: 20px;
    padding: 32px;
  }
.Modal.search{
    left: calc(50% - 500px);
    width: 1000px;
    min-height: 400px;
    top: calc(50% - 330px);
  }

.Overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0,0,0,0.5);
  }
.result-search{
    margin: 5px;
    margin-top: 32px;
    border: 1px solid #000;
  }
  #ClientProfileForm .kJdJTV{
    padding-top: 50px;
  }
 .result-search .bIxPIp{
    min-height: 200px;
    max-height: 300px;
    overflow-x: auto;
  }
  #ClientProfileForm .updated label, .updated div{
    color: #e28f0a !important;
  }
  #ClientProfileForm .validator-error-message div{
  color: red !important;
}