#ListIncomeParam{
    padding: 32px 50px
}
#ListIncomeParam ul{
    list-style-type: none;
    padding-inline-start: 20px;
    position: relative;
}
.original-tree li{
    margin: 16px 0px;
    position: relative;
    cursor: pointer;
    text-align: left;
    font-weight: bold;
    transition: 0.3s;
    min-height: 53px;
    font-weight: normal;
    font-size: 14px;
}