#TableList #top-menu{
	margin-top: 24px;
	background-color: white;
	height: 50px;
	padding: 10px 24px;
}

#TableList #export-container{
	display: inline-block;
	height: 30px;
}

#TableList #search-container{
	float: right;
}

#TableList #search-container input{
	height: 30px;
	padding: 8px;
	border: 1px solid #8c181b;
	border-radius: 6px;
}

#TableList #export-icon{
	width: 24px;
	height: 24px;
	margin-right: 14px;
}

#TableList #search-icon{
	position: relative;
	width: 18px;
	height: 18px;
	right: 24px;
}

#TableList .table {
	margin-bottom: 0;
}

#TableList thead {
	background-color: #666666;
	font-size: 13px;
	color: white;
	text-decoration: underline;
}

#TableList thead tr{
	height: 40px;
}

#TableList thead th{
	font-weight: normal;
	vertical-align: middle;
    /* text-align: center; */
}


#TableList tbody {
	background-color: white
}

#TableList tbody tr{
	height: 50px;
}

#TableList .table-striped tbody tr:nth-of-type(odd) {
    background-color: #d9d9d9;
}

#TableList #paging {
	background-color: white;
	padding: 8px;
	height: 48px;
	border: 1px solid #979797;
	margin-bottom: 32px;
}

#TableList #paging button{
	margin: 4px 4px;
	padding: 0;
	background: transparent;
	border: 0;
	color: #b9b9b9;
	outline: none;
}

#TableList #paging button.page-num{
	margin: 4px 8px;
	color: black;
}

#TableList #paging button.current{
	color: #0076ff;
}