  }
.file-input:focus{
  outline: none;
}
.Overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0,0,0,0.5);
  }
.result-search{
    margin: 5px;
