    overflow-x: auto;
  }
  #CreateStandardPricing .updated label, .updated div{
    color: #e28f0a !important;
  }
  #CreateStandardPricing .validator-error-message div{
  color: red !important;
}
.with-r-label input{
  width: calc(100% - 30px);
  border-top-right-radius: 0px;
  border-bottom-right-radius: 0px;
  border-right: 0px;
}
.with-r-label .r-label{
  border: 1px solid #979797;
  text-align: center;
  width: 30px;
  background: #ded8d8;
  color: #000;
  border-radius: 4px;
  border-top-left-radius: 0px;
  border-bottom-left-radius: 0px;
  padding: 3px 0;
}
.parent-wrapper{
  position: relative;
  overflow: hidden;
  border: 1px solid #000;
  max-width: 100%;
  box-shadow : 2px 2px 5px 1px rgba(0,0,0,0.4);
  display: block;
  transition: 0.3s;
  width: 100%;
}
.pdf-wrapper{
  position: relative;
  cursor: pointer;
  height: 100%;
}
.pdf-wrapper:hover:after{
  opacity: 1;
}

.pdf-wrapper:after{
  content:'';
  position: absolute;
  top:0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0,0,0,0.2);
  opacity: 0;
  z-index: 0;
  transition: 0.3s;
}

.parent-wrapper .delete-icon{
  position: absolute;
  background: #fff;
  padding: 5px;
  border-radius: 5px;
  top: 5px;
  right: 5px;
  width: 40px;
  height: 40px;
  z-index: 1;
  cursor: pointer;
  transition: 0.3s;
  box-shadow: inset -7px 0 14px rgba(0,0,0,0.4)
}

.parent-wrapper .delete-icon:hover{
  box-shadow: 0px 0 4px 0px #8b181c, inset -7px 0 14px rgba(0,0,0,0.4)
}
.parent-wrapper .delete-icon img{
  width: 100%;
  border-radius: 50%;
  border: 0;
}
.attachment.pdf-reader-modal{
  position: fixed;
  top: calc(50% - 40%) !important;
  left: calc(50% - 40%) !important;
  right: auto;
  bottom: auto;
  width: 80% !important;
  height: 80% !important;
  background-color: #fff;
  overflow: hidden;
  z-index: 2;
  border-radius: 0 !important;
}
.Overlay{
  z-index: 2;
}
.updated .form-input{
  color: #e28f0a !important;
}
.btnEnableContact{
  display: inline-block;
  padding: 0 7px;
  border-radius: 5px;
  cursor: pointer;
  background: #fff;
  border: 1px solid #000;
  margin-left: 20px;
  box-shadow: 2px 1px 7px 1px rgba(0,0,0,0.3);
  transition: 0.3s;
}

.btnEnableContact:hover{
  box-shadow: 2px 1px 7px 1px rgba(0,0,0,0.8);
}
.attachment .close-pdf-modal{
  position: absolute;
  right: -30px;
  top: -30px;
  font-size: 20px;
  padding: 0;
  line-height: 1;
  z-index: 4;
  cursor: pointer;
  width: 50px;
}
.pdf-reader-modal canvas{
  display: inline-block;
  justify-content: center;
  width: 100% !important;
  height: auto !important;
}
.pdf-reader-modal .react-pdf__Page{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
}
.pdfReaderWrapper{
  position: relative;
  width: 100%;
  height: 100%;
}
.img-wrapper img{
  border: 0;
}