.Modal {
    position: fixed;
    top: calc(50% - 100px);
    left: calc(50% - 329px);
    right: auto;
    bottom: auto;
    width: 658px;
    min-height: 200px;
    background-color: #fff;
    border-radius: 20px;
    padding: 32px;
  }

.Overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0,0,0,0.5);
  }
.form-input[readonly] {
	outline: none;
	color: #979797;
	background: rgba(248, 248, 248);
	cursor: auto;
}
.transparent{
  opacity: 0 !important;
}
#UserForm label{
  line-height: 1.5;
}
.updated label, .updated div{
  color :#e28f0a !important
}
.validator-error-message{
  color: red !important;
}
.updated div .validator-error-message div{
  color: red !important;
}
.updated div .form-input.emptyInput{
  color: red !important;
}
.search-user-nip input{
  width: calc(100% - 110px);
  margin-right: 20px;
}
.search-user-nip button{
  width: 90px;
}
.emptyInput{
  border-color: red;
  color: red;
}
.emptyInput::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: red;
  opacity: 1; /* Firefox */
}

.emptyInput:-ms-input-placeholder { /* Internet Explorer 10-11 */
  color: red;
}

.emptyInput::-ms-input-placeholder { /* Microsoft Edge */
  color: red;
}