.with-r-label input{
  width: calc(100% - 30px);
  border-top-right-radius: 0px;
  border-bottom-right-radius: 0px;
  border-right: 0px;
}
.with-r-label .r-label{
  border: 1px solid #979797;
  text-align: center;
  width: 30px;
  background: #ded8d8;
  color: #000;
  border-radius: 4px;
  border-top-left-radius: 0px;
  border-bottom-left-radius: 0px;
  padding: 3px 0;
}
.parent-wrapper{
  position: relative;
  overflow: hidden;
  border: 1px solid #000;
  max-width: 100%;
  box-shadow : 2px 2px 5px 1px rgba(0,0,0,0.4);
  display: block;
  transition: 0.3s;
  height: 400px;
}
.pdf-wrapper{
  position: relative;
  cursor: pointer;
  height: 100%;
}
.pdf-wrapper:hover:after{
  opacity: 1;
}

.pdf-wrapper:after{
  content:'';
  position: absolute;
  top:0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0,0,0,0.2);
  opacity: 0;
  z-index: 0;
  transition: 0.3s;
}
.parent-wrapper .delete-icon{
  position: absolute;
  background: #fff;
  padding: 5px;
  border-radius: 5px;
  top: 5px;
  right: 5px;
  width: 40px;
  height: 40px;
  z-index: 1;
  cursor: pointer;
  transition: 0.3s;
  box-shadow: inset -7px 0 14px rgba(0,0,0,0.4)
}

.parent-wrapper .delete-icon:hover{
  box-shadow: 0px 0 4px 0px #8b181c, inset -7px 0 14px rgba(0,0,0,0.4)
}
.parent-wrapper .delete-icon img{
  width: 100%;
  border-radius: 50%;
  border: 0;
}
.pdf-reader-modal{
  position: fixed;
  top: calc(50% - 40%) !important;
  left: calc(50% - 40%) !important;
  right: auto;
