#list-recipients .input {
  width: 100%;
  height: 45px;
  padding: 0 1rem;
  box-sizing: border-box;
  font: inherit;
  border-radius: 0.2rem;
  border: 2px solid #d4d5d6;
  color: #565656;
  -webkit-appearance: none;
}

#list-recipients .input:focus {
  border-color: cornflowerblue;
  outline: none;
}

#list-recipients .input.has-error {
  border-color: tomato;
}

#list-recipients .error {
  margin: 0;
  font-size: 90%;
  color: tomato;
}

#list-recipients .tag-item {
  background-color: #d4d5d6;
  display: inline-block;
  font-size: 14px;
  border-radius: 30px;
  padding: 6px 8px;
  padding-right: 6px;
  display: inline-flex;
  align-items: center;
  margin: 0 0.3rem 0.3rem 0;
}

#list-recipients .tag-item > .button {
  background-color: white;
  width: 22px;
  height: 22px;
  border-radius: 50%;
  border: none;
  cursor: pointer;
  font: inherit;
  margin-left: 10px;
  font-weight: bold;
  padding: 0;
  line-height: 1;
  display: flex;
  align-items: center;
  justify-content: center;
}

#list-recipients .tag-item-ori{
  background-color: #d4d5d6;
  display: inline-block;
  font-size: 12px;
  border-radius: 30px;
  padding: 4px 8px;
  display: inline-flex;
  align-items: center;
  margin: 0 0.3rem 0.3rem 0;
}
/* The switch - the box around the slider */
#EmailConfigForm .switch {
  position: relative;
  display: inline-block;
  width: 150px;
  height: 34px;
}
#EmailConfigForm .switch-2 {
  position: relative;
  display: inline-block;
  width: 150px;
  height: 34px;
}

/* Hide default HTML checkbox */
#EmailConfigForm .switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

/* The slider */
#EmailConfigForm .slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: .4s;
  transition: .4s;
  text-align: center;
  color: #fff;
  padding: 4px 10px;
}
#EmailConfigForm .slider-2 {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: .4s;
  transition: .4s;
  text-align: center;
  color: #fff;
  padding: 4px 10px;
}

#EmailConfigForm .slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
}

#EmailConfigForm input:checked + .slider {
  background-color: #2196F3;
}

#EmailConfigForm input:focus + .slider {
  box-shadow: 0 0 1px #2196F3;
}

#EmailConfigForm input:checked + .slider:before {
  -webkit-transform: translateX(116px);
  -ms-transform: translateX(116px);
  transform: translateX(116px);
}

/* Rounded sliders */
#EmailConfigForm .slider.round {
  border-radius: 34px;
}

#EmailConfigForm .slider.round:before {
  border-radius: 50%;
}
#EmailConfigForm .slider-2.round {
  border-radius: 34px;
}

#EmailConfigForm .slider-2.round:before {
  border-radius: 50%;
}
