.custom-card {
  padding: 8px;
  border: 1px solid #ffffff;
  border-radius: 6px;
  background-color: #dbdcdc;
  margin-top: 20px;
  margin-bottom: 20px;
  font-size: small;
}

.custom-row {
  display: flex;
  flex-direction: row;
  justify-content: start;
  align-items: baseline;
}

.custom-button {
  margin-left: 15px;
}

.custom-container {
  padding: 29px 50px;
}

.custom-margintable {
  margin-top: 25px;
}

.custom-button-add {
  right: 0;
  margin-right: -10px;
}

.custom-input {
  outline: 0;
  border-width: 0 0 2px;
  border-color: blue;
}

.custom-overflow {
  overflow: scroll;
  height: 150px;
}

.custom-dnd {
  margin: 0;
  text-align: center;
  border-style: dashed;
  border-color: red;
  width: 100%;
  height: 250px;
}

Overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0,0,0,0.5);
}
