#Sidebar {
	background-color: #6d7278;
	padding: 30px 0;
}

#Sidebar .subheader {
	opacity: 0.7;
	color: white;
	font-size: 24px;
}

#Sidebar .menu {
	width: 100%;
	border-bottom: 1px solid #979797;
}

#Sidebar .rmwb-menu .menu {
	border-bottom: none;
}

#Sidebar .menu a{
	font-size: 16px;
    color: white;
    display: block;
    width: calc(100% + 60px);
    padding: 18px 30px;
	margin: 0 -30px;
	overflow: hidden;
}
#Sidebar .menu a.is-active {
	background-color: #454a4f;

	/* Based on #Sidebar padding */
	/* margin-left: -30px; */
	/* padding: 18px 30px;
	width: 100%; */
}
#Sidebar .rmwb-menu{
	position: relative;
	margin-bottom: 0;
}

#Sidebar .rmwb-menu li{
	transition:height 0.3s linear;
	height: 0;
	opacity: 0;
}

#Sidebar .rmwb-menu.expand li{
	/* display: block; */
	height: 60px;
	border-bottom: 1px solid #979797;
	opacity: 1;
}


#Sidebar .rmwb-menu p:before{
	content: "\203A";
	position: absolute;
	top: calc(50% - 19px);
	right: 0;
	color: #fff;
	font-size: 24px;
	-webkit-transition: 0.3s linear;
	-o-transition: 0.3s linear;
	transition: 0.3s linear;
}
#Sidebar .rmwb-menu p{
	position: relative;
}
#Sidebar .rmwb-menu.expand p:before{
	-webkit-transform: rotate(90deg);
	-ms-transform: rotate(90deg);
	transform: rotate(90deg);
	-webkit-transform-origin: center center;
	-ms-transform-origin: center center;
	transform-origin: center center;
}
#Sidebar .rmwb-menu .subheader{
	padding:16px 0;
	margin-bottom: 0;

}
#Sidebar .rmwb-menu .subheader{
	border-bottom: 1px solid #979797;
}
#Sidebar .list-unstyled{
	padding: 0 30px;
	overflow: hidden;
}