.EditableItem {
    background: #fff;
    padding: 8px;
    margin: 16px 0px;
    position: relative;
    cursor: pointer;
    text-align: left;
    font-weight: bold;
    -webkit-box-shadow: 5px 5px 7px rgba(0,0,0,0.3);
            box-shadow: 5px 5px 7px rgba(0,0,0,0.3);
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
}
.level-text{
    display: inline-block;
    font-size: 14px;
    font-weight: normal;
    line-height: 1.6;
    margin-bottom: 0;
    padding: 6px
}
.Nodes{
    padding-left: 0 !important;
}
.EditableItem-Text {
    height: 100%;
    width: calc(100% - 200px);
    border: none;
    padding: 10px;
    background-color: white;
    border-bottom: 1px solid whitesmoke;
    font-size: 10pt;
}

.EditableItem-Button {
    height: 37px;
    min-width: 37px;
    font-size: 10pt;
    float: right;
    margin-left: 4px;
}

.EditableItem-Button_add:hover {
    color: black;
}

.EditableItem-Button_remove:hover {
    color: orangered;
}